<template>
  <div>
    <ul class="title-list">
      <li ><h1 class="title">MongoDB</h1></li>
    </ul>
    <div class="main-body">
      <div id="part1" class="part">
        <div class="title-lv1"> </div>
        <p class="descrp">window</p>
        <p class="title-lv2"> 1.</p>
        <pre class="code-text">
            <code>
              mongod.exe --dbpath c:\data\db
            </code>
          </pre>
        <p class="title-lv2"> 2.MongoDBWindows</p>
        <pre class="code-text">
            <code>
             mongod.exe --bind_ip yourIPadress --logpath "C:\data\dbConf\mongodb.log" --logappend --dbpath "C:\data\db"
              --port yourPortNumber --serviceName "YourServiceName" --serviceDisplayName "YourServiceName" --install
            </code>
          </pre>
        <ul class="descrp">
          <li>--bind_ip IP127.0.0.1IP</li>
          <li>--logpath MongoDB</li>
          <li>--dbpath:   </li>
          <li>--port:     27017</li>
          <li>--serviceName: </li>
          <li>--serviceDisplayName: mongodb</li>
          <li>--install: Windows</li>
        </ul>
        <p class="title-lv2"> 3.MongoDB Shell</p>
        <p class="descrp">mongodbbin</p>
        <pre class="code-text">
            <code>
              mongo
            </code>
          </pre>
      </div>
      <div id="part2" class="part">
        <div class="title-lv1"> MongoDB</div>
        <pre class="code-text">
            <code>
    show dbs                                //
    use dbname                              //
    db.dropDatabase()                       //,test
    db.collection.drop()                    //
    db.COLLECTION_NAME.insert(document)     //
    db.col.find().pretty()                  //

    //save() 
    db.collection.save(
       <span><</span>document>,
       {
         writeConcern: <span><</span>document>
       }
    )

    //remove() 
    db.collection.remove(
       <span><</span>query>,
       <span><</span>justOne>
    )

    //MongoDB AND 
    db.col.find({key1:value1, key2:value2}).pretty()

    //MongoDB OR 
    db.col.find(
       {
          $or: [
    	     {key1: value1}, {key2:value2}
          ]
       }
    ).pretty()

    //MongoDB (>)  - $gt
    db.col.find({"likes" : {$gt : 100}})       // "col"  "likes"  100 

    //MongoDBLimitlimit()MongoDB
    db.COLLECTION_NAME.find().limit(NUMBER)

    //limit()skip()skip
    db.COLLECTION_NAME.find().limit(NUMBER).skip(NUMBER)

    //MongoDBsort()sort() 1  -1  1 -1
    db.COLLECTION_NAME.find().sort({KEY:1})

    //MongoDB ensureIndex() 
    db.COLLECTION_NAME.ensureIndex({KEY:1})

    //MongoDBaggregate()
    db.COLLECTION_NAME.aggregate(AGGREGATE_OPERATION)     //sql count(*)

    //
    //$project
    db.article.aggregate(
        { $project : {
            title : 1 ,
            author : 1 ,   //_id,tilteauthor_id
        }}
     );

    //$match
    db.articles.aggregate( [
                            { $match : { score : { $gt : 70, $lte : 90 } } },
                            { $group: { _id: null, count: { $sum: 1 } } }
                           ] );


    //MongoDB
    mongod --port 27017 --dbpath "D:\set up\mongodb\data" --replSet rs0
    /*
    *rs0MongoDB27017
     mongoDB
     Mongors.initiate()
     rs.conf()
      rs.status() 
    *
    */

    //MongoDB,mongodump
    mongodump -h dbhost -d dbname -o dbdirectory

    //MongoDB,mongorestore
    mongorestore -h <span><</span>hostname><span><</span>:port> -d dbname <span><</span>path>;

    //MongoDB 
    mongostat/mongotop
            </code>
          </pre>
      </div>

